@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,opsz,wght@0,6..12,200;0,6..12,300;0,6..12,400;0,6..12,500;0,6..12,600;0,6..12,700;0,6..12,800;0,6..12,900;1,6..12,200;1,6..12,300;1,6..12,400;1,6..12,500;1,6..12,600;1,6..12,700;1,6..12,800&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Nunito Sans", sans-serif;
  -webkit-tap-highlight-color: transparent;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
  background: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

input,
textarea,
select {
  border: none;
  outline: none;
  background: none;
}

body {
  color: #7d7c7c;
  height: 100vh;
  max-height: max-content;
}

::-webkit-scrollbar {
  display: none;
}

.c-ac1 {
  color: #bc131f;
}

.c-ac2 {
  color: #ffffff;
}

.c-ac3 {
  color: #151515;
}

.bg-ac1 {
  background-color: #bc131f;
}

.bg-ac2 {
  background-color: #ffffff;
}

.bg-ac3 {
  background-color: #151515;
}

.border-ac1 {
  border: 1px solid #bc131f;
}

.border-ac2 {
  border: 1px solid #ffffff;
}

.border-ac3 {
  border: 1px solid #151515;
}

.btn-primary {
  white-space: nowrap;
  color: #ffffff;
  background-color: #bc131f;
  padding: 0.5rem 1rem;
  font-weight: 700;
  border-radius: 20px;
  border: 1px solid transparent;
  transition: all 0.2s ease-in;
}
@media (hover: none) {
  .btn-primary:active {
    transition: all 0.2s ease-out;
    background-color: transparent;
    border: 1px solid #bc131f;
    color: #bc131f;
  }
}
@media (hover: hover) {
  .btn-primary:hover {
    transition: all 0.2s ease-out;
    background-color: transparent;
    border: 1px solid #bc131f;
    color: #bc131f;
  }
}

.btn-outline {
  white-space: nowrap;
  padding: 0.5rem 1rem;
  font-weight: 700;
  border-radius: 20px;
  background-color: transparent;
  border: 1px solid #bc131f;
  transition: all 0.2s ease-in;
  color: #bc131f;
}
@media (hover: none) {
  .btn-outline:active {
    border: 1px solid transparent;
    transition: all 0.2s ease-out;
    color: #ffffff;
    background-color: #bc131f;
  }
}
@media (hover: hover) {
  .btn-outline:hover {
    border: 1px solid transparent;
    transition: all 0.2s ease-out;
    color: #ffffff;
    background-color: #bc131f;
  }
}

.btn-box-primary {
  white-space: nowrap;
