.navbar-container {
  background-color: transparent;
  padding: 20px 50px;
  border-bottom: 2px solid rgba(0, 0, 0, 0.1);
}
.navbar-container span button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 5px 0px 0px 0px;
}
.navbar-container .nav-responsive {
  display: none;
  background-color: #ffffff;
  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.1);
  width: 50%;
  position: fixed;
  height: 100vh;
  z-index: 2;
  top: 0%;
  left: -50%;
}
.navbar-container .show-nav {
  animation-name: slideRight; /* Name of the animation */
  animation-duration: 0.5s; /* Duration of the animation */
  animation-timing-function: ease; /* Timing function for smooth animation */
  animation-fill-mode: forwards; /* Keeps the final state of the animation */
}
.navbar-container .hide-nav {
  top: 0%;
  left: 0%;
  animation-name: slideLeft; /* Name of the animation */
  animation-duration: 0.5s; /* Duration of the animation */
  animation-timing-function: ease; /* Timing function for smooth animation */
  animation-fill-mode: forwards; /* Keeps the final state of the animation */
}
@keyframes slideRight {
  100% {
    left: 0%; /* End position, considering the width of the div */
  }
}
@keyframes slideLeft {
  100% {
    left: -50%; /* End position, considering the width of the div */
  }
}

.navbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  min-width: 100%;
  margin: 0 auto;
  padding: 0 20px;
  color: black;
}

.nav-left {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
}
.nav-left a {
  text-decoration: none;
}
.nav-left h1 {
  font-size: 24px;
  margin-right: 20px;
}
.nav-left ul {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
}
.nav-left li {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 5px;
  margin-right: 20px;
  position: relative;
}
.nav-left button {
  background-color: none;
  outline: none;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.nav-center {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.nav-right {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
}
.nav-right ul {
  display: hidden;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  list-style: none;
  margin: 0;
  padding: 0;
}
.nav-right li {
  font-size: 1.5rem;
}
.nav-right .hamburger {
  display: none;
  font-size: 2rem;
}

@media (max-width: 767px) {
  .navbar-container {
    overflow: hidden;
    min-width: 100vw;
    padding: 0px 0px 10px 0px;
    margin: 0;
  }
  .navbar-container span button {
    font-size: 1.5rem;
    margin: 0;
    margin-left: 2rem;
  }
  .navbar-container .nav-responsive {
    display: block;
  }
  .navbar {
    margin: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 0px 10px;
  }
  .nav-left ul {
    display: none;
    flex-direction: column;
    background-color: red;
  }
  .nav-left a h1 {
    font-size: 16px;
  }
  .nav-right ul {
    display: none;
  }
  .nav-right .hamburger {
    display: block;
    font-size: 1.5rem;
  }
  .nav-left,
  .nav-center,
  .nav-right {
    margin-top: 10px;
  }
}
.dropdown-wrapper {
  display: none;
  position: absolute;
  top: 250%;
  width: 320px;
}
.dropdown-wrapper .dropdown {
  background-color: #ffffff;
  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.1);
  padding: 20px;
  margin: 10px;
}
.dropdown-wrapper .dropdown .dropdown-content {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.show-menu {
  display: block;
}

.hide-menu {
  display: none;
}

.nav-left-mobile {
  display: block;
  position: fixed;
  width: 100%;
  top: 0;
  bottom: 0;
  background-color: white;
  overflow-y: auto;
  padding-top: 24px;
  padding-left: 4px;
  transition-duration: 0.5s;
  left: 0;
}

.product-card-container {
  max-width: 243px;
}
.product-card-container .product-card {
  border: 1px solid #d8d9da;
  padding: 0.5rem;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  transition: 0.2s ease-in-out;
  gap: 0.75rem;
}
.product-card-container .product-card:hover {
  transition: 0.2s ease-in-out;
  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.1);
}

.product-card .product-card-header {
  overflow: hidden;
  position: relative;
}
.product-card .product-card-header img {
  display: flex;
}
.product-card .product-card-header .product-card-likes-badge,
.product-card .product-card-header .product-card-stock-badge {
  top: 0.5rem;
  position: absolute;
}
.product-card .product-card-header .product-card-stock-badge {
  left: 0.5rem;
}
.product-card .product-card-header .product-card-likes-badge {
  font-weight: 600;
  font-size: 0.75rem;
  padding: 0.25rem 0.5rem;
  right: 0.5rem;
  border: 1px solid #bc131f;
  background-color: #fbd3d6;
  border-radius: 5px;
  color: #bc131f;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0.25rem;
  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.1);
}
.product-card .product-card-header .instock {
  padding: 0.25rem 0.5rem;
  border-radius: 5px;
  background-color: rgba(0, 223, 164, 0.2196078431);
  color: #00dfa4;
  border: 1px solid #00dfa4;
  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.1);
  font-weight: 600;
  font-size: 0.75rem;
}
.product-card .product-card-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 0.5rem;
}
.product-card .product-card-info h1 {
  font-size: 1rem;
}
.product-card .product-card-info h2 {
  font-size: 1rem;
}
.product-card .product-card-info .product-card-price {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}
.product-card .product-card-info .product-card-price p {
  position: relative;
}
.product-card .product-card-info .product-card-price p::after {
  content: "";
  width: 102%;
  height: 2px;
  background-color: #f1777f;
  display: block;
  position: absolute;
  transform: translateY(50%) rotateZ(-5deg);
  bottom: 50%;
}
.product-card .product-card-info .product-card-price .product-card-sale {
  background-color: #bc131f;
  color: #ffffff;
  padding: 0 0.5rem;
  border-radius: 20px;
  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.1);
}
.product-card .product-card-action {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}
.product-card .product-card-action button {
  box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.2);
  flex-basis: 100%;
}

.search-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border: 1px solid #d8d9da;
  border-radius: 0.25rem;
  width: 25rem;
  padding: 0px 20px;
  background-color: none;
}
.search-container .search-input {
  border: none;
  padding: 8px;
  width: 100%;
  outline: none;
  font-size: 16px;
}
.search-container .search-icon {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  cursor: pointer;
}
.search-container .search-icon:hover {
  color: #ccc;
  transition: background-color 0.3s;
}
@media (max-width: 767px) {
  .search-container {
    width: 100%;
    padding: 0px 5px;
  }
  .search-container .search-input {
    padding: 0;
  }
  .search-container .search-icon {
    font-size: 1.5rem;
  }
}

