.navbar-container {
  background-color: transparent;
  padding: 20px 50px;
  border-bottom: 2px solid rgba(0, 0, 0, 0.1);
  span {
    // background-color: blue;
    button {
      @include flex_layout(row, center, center);
      padding: 5px 0px 0px 0px;
    }
  }
  .nav-responsive {
    display: none;
    background-color: $theme-accent2;
    box-shadow: $box-shadow;
    width: 50%;
    position: fixed;
    height: 100vh;
    z-index: 2;
    top: 0%;
    left: -50%;
  }
  .show-nav {
    animation-name: slideRight;
    animation-duration: 0.5s;
    animation-timing-function: ease;
    animation-fill-mode: forwards;
  }
  .hide-nav {
    top: 0%;
    left: 0%;
    animation-name: slideLeft;
    animation-duration: 0.5s;
    animation-timing-function: ease;
    animation-fill-mode: forwards;
  }
  @keyframes slideRight {
    100% {
      left: 0%;
    }
  }
  @keyframes slideLeft {
    100% {
      left: -50%; /* End position, considering the width of the div */
    }
  }
}

.navbar {
  @include flex_layout(row, space-between, center);
  min-width: 100%;
  margin: 0 auto;
  padding: 0 20px;
  color: black;
}
// nav left
.nav-left {
  // background-color: green;
  @include flex_layout(row, space-between, center);
  gap: 2rem;
  a {
    text-decoration: none;
  }

  h1 {
    font-size: 24px;
    margin-right: 20px;
  }

  ul {
    @include flex_layout(row, space-between, center);
    list-style: none;
    margin: 0;
    padding: 0;
  }
  li {
    @include flex_layout(row, center, center);
    gap: 5px;
    margin-right: 20px;
    position: relative;
  }
  button {
    background-color: none;
    outline: none;
    cursor: pointer;
    @include flex_layout(row, center, center);
  }
}

.nav-center {
  // background-color: red;
  @include flex_layout(row, center, center);
}
// Nav right
.nav-right {
  @include flex_layout(row, space-between, center);
  gap: 2rem;
  ul {
    display: hidden;
    @include flex_layout(row, space-between, center);
    gap: 1rem;
    list-style: none;
    margin: 0;
    padding: 0;
  }
  li {
    font-size: 1.5rem;
  }
  .hamburger {
    display: none;
    font-size: 2rem;
  }
}
// Mobile Responsive
@include device(mobile) {
  .navbar-container {
    overflow: hidden;
    min-width: 100vw;
    padding: 0px 0px 10px 0px;

    margin: 0;
    span {
      button {
        font-size: 1.5rem;
        margin: 0;
        margin-left: 2rem;
      }
    }
    .nav-responsive {
      display: block;
    }
  }
  .navbar {
    margin: 0;
    @include flex_layout(row, center, center);
    padding: 0px 10px;
  }
  .nav-left {
    ul {
      display: none;
      flex-direction: column;
      background-color: red;
    }
    a {
      h1 {
        font-size: 16px;
      }
    }
  }
  .nav-right {
    ul {
      display: none;
    }
    .hamburger {
      display: block;
      font-size: 1.5rem;
    }
  }

  .nav-left,
  .nav-center,
  .nav-right {
    margin-top: 10px;
  }
}

//dropdown menu css

.dropdown-wrapper {
  display: none;
  position: absolute;
  top: 250%;
  width: 320px;

  .dropdown {
    background-color: $theme-accent2;
    box-shadow: $box-shadow;
    padding: 20px;
    margin: 10px;

    .dropdown-content {
      @include flex_layout(column);
    }
  }
}

.show-menu {
  display: block;
}

.hide-menu {
  display: none;
}

.nav-left-mobile {
  display: block;
  position: fixed;
  width: 100%;
  top: 0;
  bottom: 0;
  background-color: white;
  overflow-y: auto;
  padding-top: 24px;
  padding-left: 4px;
  transition-duration: 0.5s;
  left: 0;
}
